import { createContext, useContext, useState, useEffect } from "react";

const EmailContext = createContext();

export const EmailProvider = ({ children }) => {
  const [templates, setTemplates] = useState([]);
  const [campaigns, setCampaigns] = useState([]);
  const [loading, setLoading] = useState(false);

  const API_BASE = "http://localhost:5000";

  // Fetch templates
  const fetchTemplates = async () => {
    try {
      const response = await fetch(`${API_BASE}/emails/email-templates`, {
        credentials: "include",
      });
      const data = await response.json();
      if (data.success) {
        setTemplates(data.templates);
      }
    } catch (error) {
      console.error("Failed to fetch templates:", error);
    }
  };

  // Fetch campaigns
  const fetchCampaigns = async () => {
    try {
      const response = await fetch(
        `${API_BASE}/emails/email-campaigns?page=1&limit=50`,
        {
          credentials: "include",
        }
      );
      const data = await response.json();
      if (data.success) {
        setCampaigns(data.campaigns);
      }
    } catch (error) {
      console.error("Failed to fetch campaigns:", error);
    }
  };

  // Create template
  const createTemplate = async (templateData) => {
    setLoading(true);
    try {
      const response = await fetch(`${API_BASE}/emails/email-templates`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(templateData),
      });
      const data = await response.json();
      if (data.success) {
        await fetchTemplates();
        return { success: true, message: data.message };
      }
      return { success: false, message: data.message };
    } catch (error) {
      return { success: false, message: error.message };
    } finally {
      setLoading(false);
    }
  };

  // Update template
  const updateTemplate = async (id, templateData) => {
    setLoading(true);
    try {
      const response = await fetch(`${API_BASE}/emails/email-templates/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(templateData),
      });
      const data = await response.json();
      if (data.success) {
        await fetchTemplates();
        return { success: true, message: data.message };
      }
      return { success: false, message: data.message };
    } catch (error) {
      return { success: false, message: error.message };
    } finally {
      setLoading(false);
    }
  };

  // Delete template
  const deleteTemplate = async (id) => {
    try {
      const response = await fetch(`${API_BASE}/emails/email-templates/${id}`, {
        method: "DELETE",
        credentials: "include",
      });
      const data = await response.json();
      if (data.success) {
        await fetchTemplates();
        return { success: true };
      }
      return { success: false };
    } catch (error) {
      return { success: false };
    }
  };

  // Send email campaign
  const sendCampaign = async (campaignData) => {
    setLoading(true);
    try {
      const response = await fetch(`${API_BASE}/emails/email-campaigns/send`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(campaignData),
      });
      const data = await response.json();
      if (data.success) {
        await fetchCampaigns();
        return { success: true, data };
      }
      return { success: false, message: data.message };
    } catch (error) {
      return { success: false, message: error.message };
    } finally {
      setLoading(false);
    }
  };

  // Preview email
  const previewEmail = async (subject, body) => {
    try {
      const response = await fetch(
        `${API_BASE}/emails/email-campaigns/preview`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          credentials: "include",
          body: JSON.stringify({ subject, body }),
        }
      );
      const data = await response.json();
      if (data.success) {
        return { success: true, preview: data.preview };
      }
      return { success: false };
    } catch (error) {
      return { success: false };
    }
  };

  // Delete campaign
  const deleteCampaign = async (id) => {
    try {
      const response = await fetch(`${API_BASE}/emails/email-campaigns/${id}`, {
        method: "DELETE",
        credentials: "include",
      });
      const data = await response.json();
      if (data.success) {
        await fetchCampaigns();
        return { success: true };
      }
      return { success: false };
    } catch (error) {
      return { success: false };
    }
  };

  useEffect(() => {
    fetchTemplates();
    fetchCampaigns();
  }, []);

  return (
    <EmailContext.Provider
      value={{
        templates,
        campaigns,
        loading,
        createTemplate,
        updateTemplate,
        deleteTemplate,
        sendCampaign,
        previewEmail,
        deleteCampaign,
        fetchTemplates,
        fetchCampaigns,
      }}
    >
      {children}
    </EmailContext.Provider>
  );
};

export const useEmail = () => {
  const context = useContext(EmailContext);
  if (!context) {
    throw new Error("useEmail must be used within EmailProvider");
  }
  return context;
};
