import { useState } from "react";
import { Send, Eye, X, FileText } from "lucide-react";

const ComposeEmail = () => {
  const { templates, sendCampaign, previewEmail, loading } = useEmail();
  const [previewData, setPreviewData] = useState(null);
  const [formData, setFormData] = useState({
    title: "",
    subject: "",
    body: "",
    sender_email: "placement@college.edu",
    sendType: "filter",
    department: [],
    batch_year: "",
    min_cgpa: "",
    placement_status: "",
    recipient_emails: "",
    student_ids: "",
    event_id: "",
    recipient_type: "registered",
  });

  const handleUseTemplate = (template) => {
    setFormData({
      ...formData,
      title: template.template_name,
      subject: template.subject,
      body: template.body,
    });
  };

  const handlePreview = async () => {
    if (!formData.subject || !formData.body) {
      alert("Please enter subject and body");
      return;
    }
    const result = await previewEmail(formData.subject, formData.body);
    if (result.success) {
      setPreviewData(result.preview);
    }
  };

  const handleSend = async () => {
    if (!formData.title || !formData.subject || !formData.body) {
      alert("Please fill title, subject, and body");
      return;
    }
    if (!confirm("Are you sure you want to send this email?")) return;

    const payload = {
      title: formData.title,
      subject: formData.subject,
      body: formData.body,
      sender_email: formData.sender_email,
    };

    if (formData.sendType === "filter") {
      const filters = {};
      if (formData.department.length > 0)
        filters.department = formData.department;
      if (formData.batch_year)
        filters.batch_year = parseInt(formData.batch_year);
      if (formData.min_cgpa) filters.min_cgpa = parseFloat(formData.min_cgpa);
      if (formData.placement_status)
        filters.placement_status = formData.placement_status;
      payload.recipient_filter = filters;
    } else if (formData.sendType === "manual") {
      const emails = formData.recipient_emails
        .split(",")
        .map((e) => e.trim())
        .filter((e) => e);
      if (emails.length === 0) {
        alert("Please enter at least one email address");
        return;
      }
      payload.recipient_emails = emails;
    }

    const result = await sendCampaign(payload);
    if (result.success) {
      alert(
        `Email sent successfully!\nSuccessful: ${result.data.emailResults?.successful || 0}\nFailed: ${result.data.emailResults?.failed || 0}`
      );
      setFormData({
        ...formData,
        title: "",
        subject: "",
        body: "",
        department: [],
        batch_year: "",
        min_cgpa: "",
        recipient_emails: "",
      });
    } else {
      alert("Failed to send email: " + result.message);
    }
  };

  return (
    <div className="space-y-6">
      {/* Quick Templates */}
      {templates.length > 0 && (
        <div className="bg-gradient-to-r from-blue-50 to-indigo-50 rounded-xl p-6 border border-blue-100">
          <div className="flex items-center gap-2 mb-4">
            <FileText className="w-5 h-5 text-blue-600" />
            <h3 className="font-semibold text-gray-900">
              Quick Start Templates
            </h3>
          </div>
          <div className="flex flex-wrap gap-2">
            {templates.map((template) => (
              <button
                key={template.id}
                onClick={() => handleUseTemplate(template)}
                className="px-4 py-2 bg-white border border-blue-200 text-blue-700 rounded-lg text-sm font-medium hover:bg-blue-50 hover:border-blue-300 transition-all"
              >
                {template.template_name}
              </button>
            ))}
          </div>
        </div>
      )}

      <div className="bg-white rounded-xl border border-gray-200 p-6 space-y-6">
        {/* Basic Info */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-semibold text-gray-700 mb-2">
              Campaign Title
            </label>
            <input
              type="text"
              value={formData.title}
              onChange={(e) =>
                setFormData({ ...formData, title: e.target.value })
              }
              className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="e.g., Google Drive Reminder - Jan 2024"
            />
          </div>
          <div>
            <label className="block text-sm font-semibold text-gray-700 mb-2">
              Sender Email
            </label>
            <input
              type="email"
              value={formData.sender_email}
              onChange={(e) =>
                setFormData({ ...formData, sender_email: e.target.value })
              }
              className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
          </div>
        </div>

        {/* Subject */}
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-2">
            Email Subject
          </label>
          <input
            type="text"
            value={formData.subject}
            onChange={(e) =>
              setFormData({ ...formData, subject: e.target.value })
            }
            className="w-full border border-gray-300 rounded-lg px-4 py-2.5 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="Use {first_name}, {last_name}, {cgpa}, etc."
          />
        </div>

        {/* Body */}
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-2">
            Email Body (HTML)
          </label>
          <textarea
            value={formData.body}
            onChange={(e) => setFormData({ ...formData, body: e.target.value })}
            className="w-full border border-gray-300 rounded-lg px-4 py-2.5 h-48 font-mono text-sm focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="<p>Dear {first_name},</p><p>Your CGPA is {cgpa}...</p>"
          />
          <p className="text-xs text-gray-500 mt-2">
            Available variables: {"{first_name}"}, {"{last_name}"}, {"{cgpa}"},{" "}
            {"{department}"}, {"{enrollment_number}"}, {"{batch_year}"}
          </p>
        </div>

        {/* Recipients */}
        <div>
          <label className="block text-sm font-semibold text-gray-700 mb-3">
            Send To
          </label>
          <div className="flex flex-wrap gap-4 mb-4">
            {["filter", "manual"].map((type) => (
              <label key={type} className="flex items-center cursor-pointer">
                <input
                  type="radio"
                  value={type}
                  checked={formData.sendType === type}
                  onChange={(e) =>
                    setFormData({ ...formData, sendType: e.target.value })
                  }
                  className="w-4 h-4 text-blue-600 focus:ring-2 focus:ring-blue-500"
                />
                <span className="ml-2 text-sm font-medium text-gray-700 capitalize">
                  {type === "filter" ? "Filter Students" : "Manual Emails"}
                </span>
              </label>
            ))}
          </div>

          {formData.sendType === "filter" && (
            <div className="bg-gray-50 rounded-lg p-4 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Department (comma-separated)
                </label>
                <input
                  type="text"
                  value={formData.department.join(", ")}
                  onChange={(e) =>
                    setFormData({
                      ...formData,
                      department: e.target.value
                        .split(",")
                        .map((d) => d.trim()),
                    })
                  }
                  className="w-full border border-gray-300 rounded-lg px-4 py-2"
                  placeholder="CSE, ECE, ME"
                />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Batch Year
                  </label>
                  <input
                    type="number"
                    value={formData.batch_year}
                    onChange={(e) =>
                      setFormData({ ...formData, batch_year: e.target.value })
                    }
                    className="w-full border border-gray-300 rounded-lg px-4 py-2"
                    placeholder="2024"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Min CGPA
                  </label>
                  <input
                    type="number"
                    step="0.1"
                    value={formData.min_cgpa}
                    onChange={(e) =>
                      setFormData({ ...formData, min_cgpa: e.target.value })
                    }
                    className="w-full border border-gray-300 rounded-lg px-4 py-2"
                    placeholder="7.0"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Placement Status
                  </label>
                  <select
                    value={formData.placement_status}
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        placement_status: e.target.value,
                      })
                    }
                    className="w-full border border-gray-300 rounded-lg px-4 py-2"
                  >
                    <option value="">All</option>
                    <option value="eligible">Eligible</option>
                    <option value="placed">Placed</option>
                    <option value="unplaced">Unplaced</option>
                  </select>
                </div>
              </div>
            </div>
          )}

          {formData.sendType === "manual" && (
            <div className="bg-gray-50 rounded-lg p-4">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Email Addresses (comma-separated)
              </label>
              <textarea
                value={formData.recipient_emails}
                onChange={(e) =>
                  setFormData({ ...formData, recipient_emails: e.target.value })
                }
                className="w-full border border-gray-300 rounded-lg px-4 py-2 h-24"
                placeholder="student1@college.edu, student2@college.edu"
              />
            </div>
          )}
        </div>

        {/* Actions */}
        <div className="flex gap-3 pt-4 border-t">
          <button
            onClick={handlePreview}
            disabled={loading}
            className="px-6 py-2.5 bg-gray-600 text-white rounded-lg hover:bg-gray-700 disabled:opacity-50 flex items-center gap-2 font-medium transition-all"
          >
            <Eye className="w-4 h-4" />
            Preview
          </button>
          <button
            onClick={handleSend}
            disabled={loading}
            className="px-6 py-2.5 bg-gradient-to-r from-blue-600 to-indigo-600 text-white rounded-lg hover:from-blue-700 hover:to-indigo-700 disabled:opacity-50 flex items-center gap-2 font-medium transition-all shadow-lg"
          >
            <Send className="w-4 h-4" />
            {loading ? "Sending..." : "Send Email"}
          </button>
        </div>
      </div>

      {/* Preview Modal */}
      {previewData && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl max-w-3xl w-full max-h-[85vh] overflow-hidden shadow-2xl">
            <div className="flex justify-between items-center p-6 border-b">
              <h3 className="text-xl font-bold text-gray-900">Email Preview</h3>
              <button
                onClick={() => setPreviewData(null)}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <X className="w-5 h-5" />
              </button>
            </div>
            <div className="p-6 overflow-y-auto max-h-[calc(85vh-80px)] space-y-4">
              <div>
                <p className="text-sm font-semibold text-gray-600 mb-1">
                  Subject:
                </p>
                <p className="text-lg font-medium">{previewData.subject}</p>
              </div>
              <div>
                <p className="text-sm font-semibold text-gray-600 mb-2">
                  Body:
                </p>
                <div
                  className="border rounded-lg p-4 bg-gray-50"
                  dangerouslySetInnerHTML={{ __html: previewData.body }}
                />
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ComposeEmail;
