// export default EmailCampaignPage;

"use client";

import { useState } from "react";
import { Send, Mail, Users } from "lucide-react";
import ComposeEmail from "./ComposeEmail";
import Templates from "./Templates";
import CampaignHistory from "./CampaignHistory";
import { EmailProvider } from "../../context/EmailContext";

const EmailCampaignPage = () => {
  const [activeTab, setActiveTab] = useState("compose");

  const tabs = [
    { id: "compose", label: "Compose Email", icon: Send },
    { id: "templates", label: "Templates", icon: Mail },
    { id: "history", label: "Campaign History", icon: Users },
  ];

  return (
    <EmailProvider>
      <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 p-6">
        <div className="max-w-7xl mx-auto">
          {/* Header */}
          <div className="mb-8">
            <div className="flex items-center gap-3 mb-2">
              <div className="w-12 h-12 bg-gradient-to-r from-blue-600 to-indigo-600 rounded-xl flex items-center justify-center shadow-lg">
                <Mail className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-3xl font-bold text-gray-900">
                  Email Campaign Manager
                </h1>
                <p className="text-gray-600">
                  Create, manage, and track email communications
                </p>
              </div>
            </div>
          </div>

          {/* Tabs */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 mb-6 overflow-hidden">
            <div className="flex border-b border-gray-200">
              {tabs.map((tab) => {
                const Icon = tab.icon;
                return (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`flex-1 px-6 py-4 font-semibold text-sm flex items-center justify-center gap-2 transition-all ${
                      activeTab === tab.id
                        ? "bg-gradient-to-r from-blue-50 to-indigo-50 text-blue-700 border-b-2 border-blue-600"
                        : "text-gray-600 hover:bg-gray-50"
                    }`}
                  >
                    <Icon className="w-4 h-4" />
                    {tab.label}
                  </button>
                );
              })}
            </div>
          </div>

          {/* Tab Content */}
          <div>
            {activeTab === "compose" && <ComposeEmail />}
            {activeTab === "templates" && <Templates />}
            {activeTab === "history" && <CampaignHistory />}
          </div>
        </div>
      </div>
    </EmailProvider>
  );
};

export default EmailCampaignPage;
