import { Send, Mail, Users, Trash2, Clock, CheckCircle } from "lucide-react";

const CampaignHistory = () => {
  const { campaigns, deleteCampaign } = useEmail();

  const handleDelete = async (id) => {
    if (!confirm("Delete this campaign?")) return;
    const result = await deleteCampaign(id);
    if (result.success) {
      alert("Campaign deleted");
    }
  };

  return (
    <div className="space-y-4">
      {campaigns.map((campaign) => (
        <div
          key={campaign.id}
          className="bg-white rounded-xl border border-gray-200 p-6 hover:shadow-md transition-all"
        >
          <div className="flex justify-between items-start">
            <div className="flex-1">
              <div className="flex items-center gap-3 mb-2">
                <h4 className="font-bold text-gray-900 text-lg">
                  {campaign.title}
                </h4>
                <span className="inline-flex items-center gap-1 px-3 py-1 bg-green-100 text-green-700 text-xs font-medium rounded-full">
                  <CheckCircle className="w-3 h-3" />
                  Sent
                </span>
              </div>
              <p className="text-sm text-gray-600 mb-3">{campaign.subject}</p>
              <div className="flex flex-wrap gap-4 text-sm text-gray-500">
                <div className="flex items-center gap-1">
                  <Users className="w-4 h-4" />
                  {campaign.total_recipients} recipients
                </div>
                <div className="flex items-center gap-1">
                  <Clock className="w-4 h-4" />
                  {new Date(campaign.sent_at).toLocaleString()}
                </div>
                {campaign.event_title && (
                  <div className="flex items-center gap-1">
                    <Mail className="w-4 h-4" />
                    Event: {campaign.event_title}
                  </div>
                )}
              </div>
            </div>
            <button
              onClick={() => handleDelete(campaign.id)}
              className="p-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors"
              title="Delete"
            >
              <Trash2 className="w-5 h-5" />
            </button>
          </div>
        </div>
      ))}
      {campaigns.length === 0 && (
        <div className="text-center py-16 text-gray-500">
          <Send className="w-12 h-12 mx-auto mb-3 opacity-30" />
          <p>No campaigns sent yet.</p>
        </div>
      )}
    </div>
  );
};

export default CampaignHistory;
